<template>
    <div class="container-fluid border col-md-6" style="padding:10px;">
        <h3>{{label}}</h3>
        <div class="row">
            <div class="col-6">
                <p>Bootstrap Default</p>
                <span>{{defaultColor}}</span>
                <div class="rounded color-show-box border" :style="{'background-color' : defaultColor}"/>
            </div>
            <div class="col-6">
                <p>Our Theme</p>
                <input type="color" :value="customColor" @input="handleColor($event.target.value)"/>
                <input type="text" style="margin-left:5px;width:100px;height:20px;" :value="customColor" @input="handleText($event.target.value)">
                <div class="rounded color-show-box border" :style="{'background-color' : customColor}"/>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "ColorFinder",
    data : function(){
        return {
            customColor : ''
        }
    },
    props : {
        label : String,
        defaultColor : String,

    },
    watch : {
    },
    methods : {
        handleColor: function(value){
            this.customColor = value;
            this.$emit("input", value);
        },
        handleText: function(value){
            if(value.startsWith('#')){
                this.customColor = value;
            }else{
                this.customColor = '#' + value;
            }
            this.$emit("input", this.customColor);
        }
    }
}
</script>
<style lang="scss" scoped>
    .color-show-box{
        width: 150px;
        height: 150px;
        margin-top: 5px;
    }
</style>
