<template>
  <div class="container">
    <h1 class="text-dark">Color palette for sepininceer.com</h1>
    <p>In the following section we have some predefined color-sets to be used in our website. 
      For every name, we have default box to show the corresponding color in default theme of Bootstrap framework in the <strong>left</strong>.
      In the <strong>right</strong>, we have a color picker field for you to pick the right color for that name in our custom theme.
      (I have filled some of them according to the XD files you handed over previously)
    </p>
    <h2><strong>1. Named Colors</strong></h2>
    <div class="row">
          <color-finder label="Blue" default-color="#027bfd" @input="setColors('blue', $event)"/>
          <color-finder label="Indigo" default-color="#674af3" @input="setColors('indigo', $event)"/>
          <color-finder label="Purple" default-color="#6f42c1" @input="setColors('purple', $event)"/>
          <color-finder label="Pink" default-color="#e8438c" @input="setColors('pink', $event)"/>
          <color-finder label="Red" default-color="#dc3745" @input="setColors('red', $event)"/>
          <color-finder label="Orange" default-color="#fd7e14" @input="setColors('orange', $event)"/>
          <color-finder label="Yellow" default-color="#ffc114" @input="setColors('yellow', $event)"/>
          <color-finder label="Green" default-color="#28a745" @input="setColors('green', $event)"/>
          <color-finder label="Teal" default-color="#20c997" @input="setColors('teal', $event)"/>
          <color-finder label="Cyan" default-color="#17a2b8" @input="setColors('cyan', $event)"/>
    </div>

    <h2><strong>2. Tagged Colors</strong></h2>
    <div class="row">
          <color-finder label="Primary" default-color="#027bfd" @input="setColors('primary', $event)"/>
          <color-finder label="Secondary" default-color="#6d757d" @input="setColors('secondary', $event)"/>
          <color-finder label="Success" default-color="#28a745" @input="setColors('success', $event)"/>
          <color-finder label="Danger" default-color="#dc3745" @input="setColors('danger', $event)"/>
          <color-finder label="Warning" default-color="#ffc114" @input="setColors('warning', $event)"/>
          <color-finder label="Info" default-color="#17a2b8" @input="setColors('info', $event)"/>
          <color-finder label="Light" default-color="#f8f9fa" @input="setColors('light', $event)"/>
          <color-finder label="Dark" default-color="#343a40" @input="setColors('dark', $event)"/>
    </div>

    <h2><strong>3. Greys</strong></h2>
    <div class="row">
          <color-finder label="100" default-color="#f8f9fa" @input="setColors('100', $event)"/>
          <color-finder label="200" default-color="#e9ecef" @input="setColors('200', $event)"/>
          <color-finder label="300" default-color="#dee2e6" @input="setColors('300', $event)"/>
          <color-finder label="400" default-color="#ced4da" @input="setColors('400', $event)"/>
          <color-finder label="500" default-color="#aeb5bd" @input="setColors('500', $event)"/>
          <color-finder label="600" default-color="#6d757d" @input="setColors('600', $event)"/>
          <color-finder label="700" default-color="#495057" @input="setColors('700', $event)"/>
          <color-finder label="800" default-color="#212529" @input="setColors('800', $event)"/>
    </div>
    <div>
      <button @click="handleSave" class="btn btn-success" style="width:60px;position:fixed;right:50px;top:60px">Save</button>
    </div>
  </div>
  <div class="overflow-auto border bg-light"  v-html="modalMessage" style="padding:width:300px;height:350px;position:fixed;right:50px;top:250px">
  </div>
</template>

<script>
import DefaultNav from './DefaultNav.vue'
import DefaultFooter from './DefaultFooter.vue'
import ColorFinder from '@/_development/components/ColorFinder'
export default {
  data: function(){
    return {
      colors: {},
      modalShow : false,
      modalMessage : ""
    }
  },
  components: {
    DefaultNav,
    DefaultFooter,
    ColorFinder,
  },
  methods : 
  {
    setColors : function(key, value){
      if(typeof(value) == typeof(" ")){
          this.colors[key] = value;
      }
    },
    handleSave : function(){
      let colors = this.colors;
      let text = ""
      Object.keys(colors).forEach(key => {
        text += key + ' => ' + colors[key] + '<br>'
      })
      this.modalMessage = text;
      this.modalShow = true;
    }
  }
}
</script>

<style lang="scss">
/* Sticky footer */
html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 60px; /* Margin bottom equal to footer height */
}
.default-page-wrapper {
  padding: 5em 0em 3em 0em;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px; /* Footer height */
  line-height: 60px; /* Vertically center the text */
  background-color: $gray-100;
  text-align: center;
